import React from "react";
import {
    Box,
    Select,
    MenuItem,
    TextField,
    Button,
    FormControl,
    InputLabel,
} from "@mui/material";

function HeaderFilter() {
    return (
        <Box
            sx={{
                padding: "20px 0",
                display: "flex",
                gap: 2,
                flexWrap: "nowrap", //   
                justifyContent: "center",
                alignItems: "center",
                overflowX: "auto",   //     
            }}
        >

            {/*  */}
            <FormControl size="small" sx={{ minWidth: 160 }} color="success">
                <InputLabel id="region-label"></InputLabel>
                <Select labelId="region-label" defaultValue="" label="">
                    <MenuItem value=""></MenuItem>
                    <MenuItem value="seoul"></MenuItem>
                    <MenuItem value="busan"></MenuItem>
                    <MenuItem value="daegu"></MenuItem>
                    <MenuItem value="incheon"></MenuItem>
                    <MenuItem value="gwangju"></MenuItem>
                    <MenuItem value="daejeon"></MenuItem>
                    <MenuItem value="ulsan"></MenuItem>
                    <MenuItem value="gyeonggi"></MenuItem>
                    <MenuItem value="gangwon"></MenuItem>
                    <MenuItem value="chungbuk"></MenuItem>
                    <MenuItem value="chungnam"></MenuItem>
                    <MenuItem value="jeonbuk"></MenuItem>
                    <MenuItem value="jeonnam"></MenuItem>
                    <MenuItem value="gyeongbuk"></MenuItem>
                    <MenuItem value="gyeongnam"></MenuItem>
                    <MenuItem value="jeju"></MenuItem>
                </Select>
            </FormControl>

            {/*  */}
            <FormControl size="small" sx={{ minWidth: 100 }} color="success">
                <InputLabel id="season-label"></InputLabel>
                <Select labelId="season-label" defaultValue="" label="">
                    <MenuItem value=""></MenuItem>
                    <MenuItem value="spring"></MenuItem>
                    <MenuItem value="summer"></MenuItem>
                    <MenuItem value="fall"></MenuItem>
                    <MenuItem value="winter"></MenuItem>
                </Select>
            </FormControl>

            {/*   */}
            <FormControl size="small" sx={{ minWidth: 120 }} color="success">
                <InputLabel id="bari-label"></InputLabel>
                <Select labelId="bari-label" defaultValue="" label="">
                    <MenuItem value=""></MenuItem>
                    <MenuItem value="night"></MenuItem>
                    <MenuItem value="oneday"></MenuItem>
                    <MenuItem value="twoday">12</MenuItem>
                    <MenuItem value="etc"></MenuItem>
                </Select>
            </FormControl>

            {/*   */}
            <FormControl size="small" sx={{ minWidth: 120 }} color="success">
                <InputLabel id="location-label"></InputLabel>
                <Select labelId="location-label" defaultValue="" label="">
                    <MenuItem value=""></MenuItem>
                    <MenuItem value="mountain">/</MenuItem>
                    <MenuItem value="coastal"></MenuItem>
                    <MenuItem value="urban"></MenuItem>
                    <MenuItem value="straight"></MenuItem>
                    <MenuItem value="curve"></MenuItem>
                    <MenuItem value="etc"></MenuItem>
                </Select>
            </FormControl>

            {/*  cc */}
            <FormControl size="small" sx={{ minWidth: 140 }} color="success">
                <InputLabel id="cc-label"> cc</InputLabel>
                <Select labelId="cc-label" defaultValue="" label=" cc">
                    <MenuItem value=""></MenuItem>
                    <MenuItem value="125">125cc</MenuItem>
                    <MenuItem value="quarter"> (125~400cc)</MenuItem>
                    <MenuItem value="middle"> (400~900cc)</MenuItem>
                    <MenuItem value="liter"></MenuItem>
                </Select>
            </FormControl>


            {/*  */}
            <TextField size="small" placeholder=" " sx={{ width: 250 }} />

            <Button variant="contained" sx={{ backgroundColor: "#707C5C" }}>
                
            </Button>
        </Box>
    );
}

export default HeaderFilter;
