import React, { useState, useRef } from "react";
import {
    Box,
    Button,
    TextField,
    Typography,
    Paper,
    Link,
} from "@mui/material";
import { useNavigate } from "react-router-dom";


function Register() {
    const emailRef = useRef();
    const pwdRef = useRef();
    const confirmPwdRef = useRef();
    const nicknameRef = useRef();
    const inputStyle = {
        "& label.Mui-focused": {
            color: "#768265",
        },
        "& .MuiOutlinedInput-root": {
            backgroundColor: "#fff",

            "&.Mui-focused fieldset": {
                borderColor: "#768265",
            },
        },
    };

    const navigate = useNavigate();
    const [isEmailChecked, setIsEmailChecked] = useState(false);

    const [form, setForm] = useState({
        email: "",
        pwd: "",
        confirmPwd: "",
        nickname: "",
    });

    const handleChange = (e) => {
        setForm({ ...form, [e.target.name]: e.target.value });
    };

    const handleRegister = () => {
        const { email, pwd, confirmPwd, nickname } = form;

        // 1.   +  
        if (!email) {
            alert(" .");
            emailRef.current.focus();
            return;
        }
        if (!pwd) {
            alert(" .");
            pwdRef.current.focus();
            return;
        }
        if (!confirmPwd) {
            alert("  .");
            confirmPwdRef.current.focus();
            return;
        }
        if (!nickname) {
            alert("  .");
            nicknameRef.current.focus();
            return;
        }
        // 2.   
        if (pwd !== confirmPwd) {
            alert("  .");
            document.getElementsByName("confirmPwd")[0].focus();
            return;
        }
        // 3.  
        if (pwd.length < 6) {
            alert("  6  .");
            return;
        }

        // 4.  
        if (!isEmailChecked) {
            alert("   .");
            return;
        }


        // 5.     
        fetch("http://localhost:3005/user/register", {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
            },
            body: JSON.stringify({
                email: form.email,
                pwd: form.pwd,
                nickname: form.nickname,
            }),
        })
            .then((res) => res.json())
            .then((data) => {
                console.log(" :", data);
                if (data.success) {
                    alert(" !");
                    navigate("/login")
                } else {
                    alert(data.message || " .");
                }
            })
            .catch((err) => {
                console.error("    :", err);
                alert("  .");
            });
    };

    return (
        <Box
            sx={{
                backgroundColor: "#fdfbf5",
                minHeight: "100vh",
                display: "flex",
                flexDirection: "column",
                alignItems: "center",
                justifyContent: "center",
            }}
        >
            <Paper
                elevation={3}
                sx={{
                    padding: 4,
                    width: 300,
                    textAlign: "center",
                    backgroundColor: "#fffef6",
                }}
            >
                <Typography
                    variant="subtitle1"
                    sx={{
                        mb: 2,
                        fontWeight: "bold",
                        color: "#768265",
                        borderRadius: "10px",
                        padding: "5px 10px",
                        fontSize: "0.9rem",
                    }}
                >
                      ,   
                </Typography>

                <img
                    src="/img/mubok-logo1.png"
                    alt=" "
                    style={{ width: 120, marginBottom: 16 }}
                />

                <Typography variant="h6" sx={{ mb: 2, fontWeight: "bold" }}>
                    
                </Typography>

                <Box sx={{ display: "flex", flexDirection: "column", gap: 2, mb: 2 }}>

                    <Box sx={{ position: "relative", mb: 1 }}>
                        <TextField
                            inputRef={emailRef}
                            fullWidth
                            name="email"
                            label=""
                            variant="outlined"
                            value={form.email}
                            onChange={(e) => {
                                setForm({ ...form, email: e.target.value });
                                setIsEmailChecked(false);
                            }}
                            InputProps={{
                                readOnly: isEmailChecked,
                                style: {
                                    backgroundColor: isEmailChecked ? "#f8f8f8" : "#fff",
                                    cursor: isEmailChecked ? "default" : "text"
                                }
                            }}
                            sx={inputStyle}
                        />

                        {isEmailChecked && (
                            <Button
                                size="small"
                                onClick={() => {
                                    setIsEmailChecked(false);
                                    setTimeout(() => emailRef.current?.focus(), 0);
                                }}
                                sx={{
                                    mt: 0.5,
                                    mb: 1,
                                    fontSize: "0.8rem",
                                    color: "#768265",
                                    textTransform: "none"
                                }}
                            >
                                 
                            </Button>
                        )}
                    </Box>
                    <Button
                        variant="outlined"
                        fullWidth
                        onClick={() => {
                            if (!form.email) return alert(" .");
                            fetch(`http://localhost:3005/user/check-email?email=${form.email}`)
                                .then((res) => res.json())
                                .then((data) => {
                                    alert(data.message);
                                    if (data.success) setIsEmailChecked(true);
                                })
                                .catch(() => alert("    "));
                        }}
                        sx={{
                            color: "#94b8c4",
                            fontWeight: "bold",
                            borderWidth: "2px",
                            borderColor: "#94b8c4",       //  !
                            "&:hover": {
                                backgroundColor: "#94b8c4",
                                color: "white",
                                borderColor: "#94b8c4",     //  hover   
                            },
                        }}
                    >
                        
                    </Button>

                    <TextField
                        inputRef={pwdRef}
                        fullWidth
                        name="pwd"
                        label=""
                        type="password"
                        variant="outlined"
                        value={form.pwd}
                        onChange={handleChange}
                        error={form.pwd.length > 0 && form.pwd.length < 6}
                        helperText={
                            form.pwd.length > 0 && form.pwd.length < 6
                                ? "  6  ."
                                : " "
                        }
                        sx={inputStyle}
                    />
                    <TextField
                        inputRef={confirmPwdRef}
                        fullWidth
                        name="confirmPwd"
                        label=" "
                        type="password"
                        variant="outlined"
                        value={form.confirmPwd}
                        onChange={handleChange}
                        error={form.confirmPwd.length > 0 && form.confirmPwd !== form.pwd}
                        helperText={
                            form.confirmPwd.length > 0 && form.confirmPwd !== form.pwd
                                ? "  ."
                                : " "
                        }
                        sx={inputStyle}
                    />
                    <TextField
                        inputRef={nicknameRef}
                        fullWidth
                        name="nickname"
                        label=""
                        variant="outlined"
                        value={form.nickname}
                        onChange={handleChange}
                        sx={inputStyle}
                    />
                </Box>

                <Button
                    fullWidth
                    variant="contained"
                    onClick={handleRegister}
                    sx={{
                        mt: 3,
                        backgroundColor: "#94b8c4",
                        color: "white",
                        fontWeight: "bold",
                        "&:hover": {
                            backgroundColor: "#4f7f91",
                        },
                    }}
                >
                    
                </Button>
                <Typography variant="body2" marginTop={"32px"}>
                      ?{" "}
                    <Link href="/login" underline="hover">
                        
                    </Link>
                </Typography>
            </Paper>
        </Box>
    );
}

const inputStyle = {
    mb: 2,
    "& label.Mui-focused": {
        color: "#768265",
    },
    "& .MuiOutlinedInput-root": {
        "&.Mui-focused fieldset": {
            borderColor: "#768265",
        },
    },
};

export default Register;