import React, { useState,useEffect } from "react";
import {
    Box,
    TextField,
    Button,
    Typography,
    Select,
    MenuItem,
    IconButton,
} from "@mui/material";
import CloseIcon from "@mui/icons-material/Close";

import KakaoMap from "../components/KakaoMap";
import { useNavigate } from "react-router-dom";
import { useRecoilValue } from "recoil";
import { userState } from "../recoil/atoms";


function FeedWrite() {
    const navigate = useNavigate();
    const user = useRecoilValue(userState);
    const currentUserId = user.member_no;

    const [title, setTitle] = useState("");
    const [content, setContent] = useState("");

    const [region, setRegion] = useState("");
    const [season, setSeason] = useState("");
    const [bariType, setBariType] = useState("");
    const [locationType, setLocationType] = useState("");
    const [ccType, setCcType] = useState("");

    const [startPlace, setStartPlace] = useState({ name: "", lat: null, lng: null });
    const [waypoints, setWaypoints] = useState([]);
    const [endPlace, setEndPlace] = useState({ name: "", lat: null, lng: null });
    const [courseList, setCourseList] = useState([]);
    const [searchPlaces, setSearchPlaces] = useState([]);
    const [selectedPlaceType, setSelectedPlaceType] = useState(null);

    const [images, setImages] = useState([]);
    const [previews, setPreviews] = useState([]);

    const handleImageChange = (e) => {
        const files = Array.from(e.target.files);
        setImages(prev => [...prev, ...files]);
        const newPreviews = files.map(file => URL.createObjectURL(file));
        setPreviews(prev => [...prev, ...newPreviews]);
    };

    const handleRemoveImage = (indexToRemove) => {
        setImages(prev => prev.filter((_, i) => i !== indexToRemove));
    };

    const handleSearch = () => {
        setSearchPlaces([
            { type: "start", keyword: startPlace.name },
            ...waypoints.map((wp, i) => ({ type: `waypoint_${i}`, keyword: wp.name })),
            { type: "end", keyword: endPlace.name },
        ]);
    };

    const handleSubmit = () => {
        console.log(currentUserId);
        const feedData = {
            member_no: currentUserId,
            title,
            content,
            region,
            season,
            bariType,
            locationType,
            ccType,
            courseList: courseList.filter(p => p?.name && p?.lat && p?.lng),
        };

        //     
        fetch("/feed", {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify(feedData),
        })
            .then(res => res.json())
            .then(data => {
                console.log("insertId===>", data.insertId);
                const feedNo = data.insertId;

                //   
                const formData = new FormData();
                images.forEach(file => formData.append("files", file));
                formData.append("feed_no", feedNo);

                return fetch("/feed/upload", {
                    method: "POST",
                    body: formData,
                });
            })
            .then(res => res.json())
            .then(uploadResult => {
                console.log("  :", uploadResult);
                alert("  !");
                navigate("/home");
            })
            .catch(err => {
                console.error("  :", err);
            });

    };
    useEffect(() => {
            if (!user.isLogin) {
                alert("  .");
                navigate("/login");
            }
        }, []);

    return (
        <Box sx={{ p: 4 }}>
            <Box sx={{ maxWidth: "800px", width: "100%", margin: "0 auto", p: 4 }}>
            <Typography variant="h5" fontWeight="bold" mb={2}>  </Typography>
                {/*   */}
                <Box sx={{ display: "flex", flexDirection: "column", gap: 2, mb: 3 }}>
                    <Select value={region} onChange={(e) => setRegion(e.target.value)} displayEmpty size="small" fullWidth>
                        <MenuItem value="" disabled> </MenuItem>
                        <MenuItem value="seoul"></MenuItem>
                        <MenuItem value="busan"></MenuItem>
                        <MenuItem value="daegu"></MenuItem>
                        <MenuItem value="incheon"></MenuItem>
                        <MenuItem value="gwangju"></MenuItem>
                        <MenuItem value="daejeon"></MenuItem>
                        <MenuItem value="ulsan"></MenuItem>
                        <MenuItem value="gyeonggi"></MenuItem>
                        <MenuItem value="gangwon"></MenuItem>
                        <MenuItem value="chungbuk"></MenuItem>
                        <MenuItem value="chungnam"></MenuItem>
                        <MenuItem value="jeonbuk"></MenuItem>
                        <MenuItem value="jeonnam"></MenuItem>
                        <MenuItem value="gyeongbuk"></MenuItem>
                        <MenuItem value="gyeongnam"></MenuItem>
                        <MenuItem value="jeju"></MenuItem>
                    </Select>
                    <Select value={season} onChange={(e) => setSeason(e.target.value)} displayEmpty size="small" fullWidth>
                        <MenuItem value="" disabled> </MenuItem>
                        <MenuItem value="spring"></MenuItem>
                        <MenuItem value="summer"></MenuItem>
                        <MenuItem value="fall"></MenuItem>
                        <MenuItem value="winter"></MenuItem>
                    </Select>
                    <Select value={bariType} onChange={(e) => setBariType(e.target.value)} displayEmpty size="small" fullWidth>
                        <MenuItem value="" disabled> </MenuItem>
                        <MenuItem value="night"></MenuItem>
                        <MenuItem value="oneday"></MenuItem>
                        <MenuItem value="twoday">12</MenuItem>
                        <MenuItem value="etc"></MenuItem>
                    </Select>
                    <Select value={locationType} onChange={(e) => setLocationType(e.target.value)} displayEmpty size="small" fullWidth>
                        <MenuItem value="" disabled> </MenuItem>
                        <MenuItem value="mountain">/</MenuItem>
                        <MenuItem value="coastal"></MenuItem>
                        <MenuItem value="urban"></MenuItem>
                        <MenuItem value="straight"></MenuItem>
                        <MenuItem value="curve"></MenuItem>
                        <MenuItem value="etc"></MenuItem>
                    </Select>
                    <Select value={ccType} onChange={(e) => setCcType(e.target.value)} displayEmpty size="small" fullWidth>
                        <MenuItem value="" disabled> cc</MenuItem>
                        <MenuItem value="125">125cc</MenuItem>
                        <MenuItem value="quarter"> (125~400cc)</MenuItem>
                        <MenuItem value="middle"> (400~900cc)</MenuItem>
                        <MenuItem value="liter"></MenuItem>
                    </Select>
                </Box>

                {/*   */}
                <Typography variant="h6" fontWeight="bold" mb={1}>1. </Typography>
                <Box sx={{ display: "flex", alignItems: "center", gap: 1, mb: 1 }}>
                    <TextField
                        label=""
                        size="small"
                        value={startPlace?.name || ""}
                        onChange={(e) => setStartPlace({ ...startPlace, name: e.target.value })}
                        onFocus={() => setSelectedPlaceType("start")}
                    />
                </Box>
                <Box sx={{ display: "flex", alignItems: "center", gap: 1, mb: 1 }}>
                    {waypoints.map((wp, idx) => (
                        <TextField
                            key={idx}
                            label={` ${idx + 1}`}
                            size="small"
                            value={wp?.name || ""}
                            onChange={(e) => {
                                const updated = [...waypoints];
                                updated[idx] = { ...updated[idx], name: e.target.value };
                                setWaypoints(updated);
                            }}
                            onFocus={() => setSelectedPlaceType(`waypoint_${idx}`)}
                        />
                    ))}
                    <Button onClick={() => setWaypoints([...waypoints, { name: "", lat: null, lng: null }])}>
                         
                    </Button>
                </Box>

                <Box sx={{ display: "flex", alignItems: "center", gap: 1, mb: 2 }}>
                    <TextField
                        label=""
                        size="small"
                        value={endPlace?.name || ""}
                        onChange={(e) => setEndPlace({ ...endPlace, name: e.target.value })}
                        onFocus={() => setSelectedPlaceType("end")}
                    />

                    <Button variant="outlined" onClick={handleSearch}></Button>
                </Box>

                {/*  API */}
                <Box sx={{ width: "100%", height: 300, mb: 3 }}>
                    <KakaoMap
                        searchPlaces={searchPlaces}
                        selectedPlaceType={selectedPlaceType}
                        onSelectPlace={(placeType, placeObj) => {
                            if (placeType === "start") {
                                setStartPlace(placeObj);
                            } else if (placeType?.startsWith("waypoint_")) {
                                const index = Number(placeType.split("_")[1]);
                                const updated = [...waypoints];
                                updated[index] = placeObj;
                                setWaypoints(updated);
                            } else if (placeType === "end") {
                                setEndPlace(placeObj);
                            }

                            setCourseList((prev) => {
                                const index =
                                    placeType === "start"
                                        ? 0
                                        : placeType === "end"
                                            ? waypoints.length + 1
                                            : Number(placeType.split("_")[1]) + 1;

                                const updated = [...prev];
                                updated[index] = placeObj;
                                return updated;
                            });
                        }}
                    />
                </Box>

                {/*  */}
                <Typography variant="h6" fontWeight="bold" mb={1}>2.  ()</Typography>
                <TextField
                    fullWidth
                    placeholder=" "
                    sx={{ mb: 3 }}
                    value={title}
                    onChange={(e) => setTitle(e.target.value)}
                />

                {/*  */}
                <Typography variant="h6" fontWeight="bold" mb={1}>3. </Typography>
                <TextField
                    fullWidth
                    multiline
                    rows={4}
                    placeholder="  ."
                    sx={{ mb: 3 }}
                    value={content}
                    onChange={(e) => setContent(e.target.value)}
                />

                {/*   */}
                <Typography variant="h6" fontWeight="bold" mb={1}>4. </Typography>
                {/*   */}
                <Box sx={{ display: "flex", gap: 2, flexWrap: "wrap", mb: 2 }}>
                    {images.map((img, index) => (
                        <Box
                            key={index}
                            sx={{
                                position: "relative",
                                width: 100,
                                height: 100,
                                borderRadius: 2,
                                overflow: "hidden",
                                border: "1px solid #ccc",
                            }}
                        >
                            <img
                                src={URL.createObjectURL(img)}
                                alt={`preview-${index}`}
                                style={{ width: "100%", height: "100%", objectFit: "cover" }}
                            />
                            <IconButton
                                onClick={() => handleRemoveImage(index)}
                                sx={{
                                    position: "absolute",
                                    top: 4,
                                    right: 4,
                                    backgroundColor: "#000",
                                    color: "#fff",
                                    width: 24,
                                    height: 24,
                                    "&:hover": { backgroundColor: "#333" },
                                }}
                            >
                                <CloseIcon sx={{ fontSize: 16 }} />
                            </IconButton>
                        </Box>
                    ))}
                </Box>
                {/*    input */}
                <input
                    id="fileInput"
                    type="file"
                    accept="image/*"
                    multiple
                    style={{ display: "none" }}
                    onChange={(e) => {
                        const files = Array.from(e.target.files);
                        setImages(files);
                    }}
                />

                {/*    */}
                <Button
                    variant="outlined"
                    onClick={() => document.getElementById("fileInput").click()}
                >
                     
                </Button>


                <Box textAlign="right">
                    <Button variant="contained" onClick={handleSubmit}></Button>
                </Box>
            </Box>
        </Box>
    );
}

export default FeedWrite;
