import React, { useState, useEffect } from "react";
import { useParams, useNavigate } from "react-router-dom";
import { useLocation } from "react-router-dom";

import {
    Box, Typography, TextField, Button, Select, MenuItem
} from "@mui/material";
import KakaoMap from "../components/KakaoMap";

function FeedEdit() {
    const { no } = useParams();
    const navigate = useNavigate();

    const [title, setTitle] = useState("");
    const [content, setContent] = useState("");
    const [region, setRegion] = useState("");
    const [season, setSeason] = useState("");
    const [bariType, setBariType] = useState("");
    const [locationType, setLocationType] = useState("");
    const [ccType, setCcType] = useState("");

    const [startPlace, setStartPlace] = useState({ name: "", lat: null, lng: null });
    const [waypoints, setWaypoints] = useState([]);
    const [endPlace, setEndPlace] = useState({ name: "", lat: null, lng: null });
    const [searchPlaces, setSearchPlaces] = useState([]);
    const [selectedPlaceType, setSelectedPlaceType] = useState(null);
    const [courseList, setCourseList] = useState([]);

    const location = useLocation();

    useEffect(() => {
        fetch(`/feed/${no}`)
            .then(res => res.json())
            .then(data => {
                const feed = data.info;
                console.log("feed ===> ", feed);
                setTitle(feed.title);
                setContent(feed.content);
                setRegion(feed.region);
                setSeason(feed.season);
                setBariType(feed.bari_type);
                setLocationType(feed.place_type);
                setCcType(feed.bike_cc);

                const fullCourse = data.course.map(item => ({
                    name: item.place_name,
                    lat: item.latitude,
                    lng: item.longitude,
                }));

                setCourseList(fullCourse);

                if (fullCourse.length >= 2) {
                    setStartPlace(fullCourse[0]);
                    setEndPlace(fullCourse[fullCourse.length - 1]);
                    setWaypoints(fullCourse.slice(1, -1));
                } else if (fullCourse.length === 1) {
                    setStartPlace(fullCourse[0]);
                    setEndPlace({});
                    setWaypoints([]);
                } else {
                    setStartPlace({});
                    setEndPlace({});
                    setWaypoints([]);
                }


            });

    }, [no]);


    const handleSearch = () => {
        setSearchPlaces([
            { type: "start", keyword: startPlace.name },
            ...waypoints.map((wp, i) => ({ type: `waypoint_${i}`, keyword: wp.name })),
            { type: "end", keyword: endPlace.name },
        ]);
    };

    const handleSubmit = () => {
        const updated = {
            title, content, region, season, bariType, locationType, ccType,
            courseList: courseList.filter(p => p?.name && p?.lat && p?.lng),
        };

        fetch(`/feed/${no}`, {
            method: "PUT",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify(updated),
        })
            .then(res => res.json())
            .then(data => {
                alert(" !");
                navigate(`/feed/${no}`, {
                    state: { backgroundLocation: { pathname: "/home" } },
                    replace: true, //     
                });
            });

    };

    return (
        <Box sx={{ p: 4 }}>
            <Typography variant="h5" fontWeight="bold" mb={2}> </Typography>

            <TextField fullWidth label="" value={title} onChange={(e) => setTitle(e.target.value)} sx={{ mb: 2 }} />
            <TextField fullWidth multiline rows={4} label="" value={content} onChange={(e) => setContent(e.target.value)} sx={{ mb: 2 }} />

            {/*  */}
            <Box sx={{ display: "flex", flexDirection: "column", gap: 2, mb: 3 }}>

                <Select value={region} onChange={(e) => setRegion(e.target.value)} displayEmpty size="small" fullWidth>
                    <MenuItem value="" disabled> </MenuItem>
                    <MenuItem value="seoul"></MenuItem>
                    <MenuItem value="busan"></MenuItem>
                    <MenuItem value="daegu"></MenuItem>
                    <MenuItem value="incheon"></MenuItem>
                    <MenuItem value="gwangju"></MenuItem>
                    <MenuItem value="daejeon"></MenuItem>
                    <MenuItem value="ulsan"></MenuItem>
                    <MenuItem value="gyeonggi"></MenuItem>
                    <MenuItem value="gangwon"></MenuItem>
                    <MenuItem value="chungbuk"></MenuItem>
                    <MenuItem value="chungnam"></MenuItem>
                    <MenuItem value="jeonbuk"></MenuItem>
                    <MenuItem value="jeonnam"></MenuItem>
                    <MenuItem value="gyeongbuk"></MenuItem>
                    <MenuItem value="gyeongnam"></MenuItem>
                    <MenuItem value="jeju"></MenuItem>
                </Select>
                <Select value={season} onChange={(e) => setSeason(e.target.value)} displayEmpty size="small" fullWidth>
                    <MenuItem value="" disabled> </MenuItem>
                    <MenuItem value="spring"></MenuItem>
                    <MenuItem value="summer"></MenuItem>
                    <MenuItem value="fall"></MenuItem>
                    <MenuItem value="winter"></MenuItem>
                </Select>
                <Select value={bariType} onChange={(e) => setBariType(e.target.value)} displayEmpty size="small" fullWidth>
                    <MenuItem value="" disabled> </MenuItem>
                    <MenuItem value="night"></MenuItem>
                    <MenuItem value="oneday"></MenuItem>
                    <MenuItem value="twoday">12</MenuItem>
                    <MenuItem value="etc"></MenuItem>
                </Select>
                <Select value={locationType} onChange={(e) => setLocationType(e.target.value)} displayEmpty size="small" fullWidth>
                    <MenuItem value="" disabled> </MenuItem>
                    <MenuItem value="mountain">/</MenuItem>
                    <MenuItem value="coastal"></MenuItem>
                    <MenuItem value="urban"></MenuItem>
                    <MenuItem value="straight"></MenuItem>
                    <MenuItem value="curve"></MenuItem>
                    <MenuItem value="etc"></MenuItem>
                </Select>
                <Select value={ccType} onChange={(e) => setCcType(e.target.value)} displayEmpty size="small" fullWidth>
                    <MenuItem value="" disabled> cc</MenuItem>
                    <MenuItem value="125">125cc</MenuItem>
                    <MenuItem value="quarter"> (125~400cc)</MenuItem>
                    <MenuItem value="middle"> (400~900cc)</MenuItem>
                    <MenuItem value="liter"></MenuItem>
                </Select>
            </Box>


            {/*    */}
            <Typography variant="h6" fontWeight="bold" mb={1}> </Typography>

            <TextField fullWidth label="" value={startPlace.name || ""} onChange={(e) => setStartPlace({ ...startPlace, name: e.target.value })} onFocus={() => setSelectedPlaceType("start")} sx={{ mb: 1 }} />

            {waypoints.map((wp, i) => (
                <TextField
                    key={i}
                    fullWidth
                    label={` ${i + 1}`}
                    value={wp.name || ""}
                    onChange={(e) => {
                        const updated = [...waypoints];
                        updated[i] = { ...updated[i], name: e.target.value };
                        setWaypoints(updated);
                    }}
                    onFocus={() => setSelectedPlaceType(`waypoint_${i}`)}
                    sx={{ mb: 1 }}
                />
            ))}

            <TextField fullWidth label="" value={endPlace.name || ""} onChange={(e) => setEndPlace({ ...endPlace, name: e.target.value })} onFocus={() => setSelectedPlaceType("end")} sx={{ mb: 2 }} />

            <Button variant="outlined" onClick={handleSearch} sx={{ mb: 3 }}> </Button>

            <Box sx={{ width: "100%", height: 300, mb: 3 }}>
                <KakaoMap
                    searchPlaces={searchPlaces}
                    selectedPlaceType={selectedPlaceType}
                    onSelectPlace={(placeType, placeObj) => {
                        if (placeType === "start") setStartPlace(placeObj);
                        else if (placeType?.startsWith("waypoint_")) {
                            const index = Number(placeType.split("_")[1]);
                            const updated = [...waypoints];
                            updated[index] = placeObj;
                            setWaypoints(updated);
                        } else if (placeType === "end") setEndPlace(placeObj);

                        setCourseList((prev) => {
                            const index =
                                placeType === "start" ? 0 :
                                    placeType === "end" ? waypoints.length + 1 :
                                        Number(placeType.split("_")[1]) + 1;

                            const updated = [...prev];
                            updated[index] = placeObj;
                            return updated;
                        });
                    }}
                />
            </Box>

            <Box textAlign="right">
                <Button variant="contained" onClick={handleSubmit}> </Button>
            </Box>
        </Box>
    );
}

export default FeedEdit;
