import React, { useState, useEffect } from "react";
import { useNavigate, useParams, useLocation } from "react-router-dom";

import { useRecoilValue } from "recoil";
import { userState } from "../recoil/atoms";

// mui
import {
    Box,
    Typography,
    Divider,
    TextField,
    Button,
    IconButton,
    Avatar
} from "@mui/material";
import FavoriteBorderIcon from "@mui/icons-material/FavoriteBorder";
import ChatBubbleOutlineIcon from "@mui/icons-material/ChatBubbleOutline";
import SendIcon from "@mui/icons-material/Send";
import CloseIcon from "@mui/icons-material/Close";
import FavoriteIcon from "@mui/icons-material/Favorite";
import BookmarkIcon from "@mui/icons-material/Bookmark";
import BookmarkBorderIcon from "@mui/icons-material/BookmarkBorder";

// 
import FeedDetailImageSlider from "../components/FeedDetailImageSlider"; //  
import KakaoCourseMap from "../components/KakaoCourseMap"; //   
import FeedOptions from "../components/FeedOptions";



function FeedDetail() {
    // 
    const location = useLocation();
    const navigate = useNavigate();
    const background = location.state?.backgroundLocation;

    const handleClose = () => {
        navigate(background || "/home", { replace: true });
    };

    const user = useRecoilValue(userState);
    const currentUserId = user.member_no;

    //
    const { no } = useParams();
    const [feed, setFeed] = useState(null);
    //  
    const [comment, setComment] = useState("");
    const [comments, setComments] = useState([]);
    // 
    const [images, setImages] = useState([]);
    // 
    const [courseList, setCourseList] = useState([]);
    // esc   
    useEffect(() => {
        const handleKeyDown = (e) => {
            if (e.key === "Escape") {
                navigate(background || "/home", { replace: true });
            }
        };
        window.addEventListener("keydown", handleKeyDown);

        //     
        return () => {
            window.removeEventListener("keydown", handleKeyDown);
        };
    }, []);

    useEffect(() => {
        fetch(`http://localhost:3005/feed/${no}`)
            .then(res => res.json())
            .then(data => {
                console.log("course ===>", data.course)
                if (data.success) {
                    setFeed(data.info);        //  
                    setImages(data.images);    //  
                    setComments(data.comments); //  
                    setCourseList(data.course); //  
                }
            });
    }, [no]);

    const handleAddComment = () => {
        if (!comment.trim()) return;

        fetch(`/feed/${no}/comment`, {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify({ member_no: 1, content: comment.trim() }),
        })
            .then(res => res.json())
            .then(data => {
                if (data.success) {
                    setComment("");
                    //    
                    setComments(prev => [...prev, {
                        member_no: 1,
                        content: comment.trim(),
                        cdatetime: new Date().toISOString(),
                    }]);
                }
            });
    };

    // 
    const [liked, setLiked] = useState(false);
    const [likeCount, setLikeCount] = useState(50); //   
    const handleToggleLike = () => {
        setLiked(!liked);
        setLikeCount(prev => liked ? prev - 1 : prev + 1);
    };

    // 
    const [bookmarked, setBookmarked] = useState(false);
    const handleToggleBookmark = () => {
        setBookmarked(prev => !prev);
    };

    return (
        <Box
            sx={{
                position: "fixed",
                inset: 0,
                backgroundColor: "rgba(0,0,0,0.4)", //  
                zIndex: 1300,
            }}
        >
            {/*   */}
            <IconButton
                onClick={handleClose}
                sx={{
                    position: "absolute",
                    top: 20,
                    right: 20,
                    color: "#fff",
                    backgroundColor: "rgba(0, 0, 0, 0.77)",
                    zIndex: 1400,
                }}
            >
                <CloseIcon />
            </IconButton>

            <Box
                sx={{
                    position: "absolute",
                    top: "50%",
                    left: "50%",
                    transform: "translate(-50%, -50%)",
                    backgroundColor: "#fff",
                    width: "min(95vw, 900px)",        //  :    
                    height: "min(90vh, 600px)",       //    +  600px 
                    display: "grid",                  //  grid   
                    gridTemplateColumns: "1fr 350px", //  : / :
                    borderRadius: "8px",
                    overflow: "hidden",
                    zIndex: 1350,
                }}
            >

                {/*   */}
                <Box sx={{ flex: 1, display: "flex", flexDirection: "column" }}>
                    <Box sx={{ flex: 1 }}>
                        <FeedDetailImageSlider images={images} courseList={courseList} />
                    </Box>
                </Box>
                {/*   */}
                <Box sx={{ width: 350, display: "flex", flexDirection: "column", height: "100%" }}>
                    {feed && (
                        <>
                            {/* 1.   */}
                            <Box sx={{ padding: 2 }}>
                                <Box
                                    sx={{
                                        display: "flex",
                                        justifyContent: "space-between",
                                        alignItems: "center",
                                        mb: 1,
                                    }}
                                >
                                    {/* :  +  */}
                                    <Box sx={{ display: "flex", alignItems: "center", gap: 1 }}>
                                        <Avatar
                                            src={
                                                user.profile_img
                                                    ? `http://localhost:3005/uploads/profile/${user.profile_img}`
                                                    : "default.png"
                                            }
                                        />
                                        <Typography fontWeight="bold">{feed.nickname}</Typography>
                                        <Typography variant="caption" color="text.secondary">
                                            {new Date(feed.cdatetime).toLocaleDateString()}
                                        </Typography>
                                    </Box>

                                    {/* :   */}
                                    <FeedOptions feed={feed} />
                                </Box>

                                {/* 2.  */}
                                <Typography variant="body2" sx={{ mt: 2 }}>
                                    <strong>{feed.nickname}</strong> {feed.title}<br />
                                    {feed.content}
                                </Typography>
                                <Typography variant="body2" sx={{ color: "#888", mt: 1 }}>
                                    #{feed.region} #{feed.season} #{feed.place_type}
                                </Typography>
                            </Box>

                            {/* 3. / */}
                            <Box sx={{ px: 2 }}>
                                <Divider sx={{ my: 2 }} />
                                <Box sx={{ display: "flex", justifyContent: "space-between", mb: 1 }}>
                                    <Box>
                                        <IconButton onClick={handleToggleLike}>
                                            {liked ? (
                                                <FavoriteIcon sx={{ color: "#e74c3c" }} />
                                            ) : (
                                                <FavoriteBorderIcon sx={{ color: "#94B8C4" }} />
                                            )}
                                        </IconButton>

                                        <IconButton><ChatBubbleOutlineIcon sx={{ color: "#94B8C4" }} /></IconButton>
                                        <IconButton><SendIcon sx={{ color: "#94B8C4" }} /></IconButton>
                                    </Box>
                                    <IconButton onClick={handleToggleBookmark}>
                                        {bookmarked ? (
                                            <BookmarkIcon sx={{ color: "#707C5C" }} />
                                        ) : (
                                            <BookmarkBorderIcon sx={{ color: "#94B8C4" }} />
                                        )}
                                    </IconButton>
                                </Box>

                                <Typography variant="caption" fontWeight="bold" mb={1}>
                                     {likeCount}
                                </Typography>
                            </Box>
                        </>
                    )}

                    {/* 4.   */}
                    <Box sx={{ flex: 1, px: 2, overflowY: "auto", mb: 1 }}>
                        {comments.map((c, i) => (
                            <Box key={i} sx={{ mb: 1 }}>
                                <Typography variant="body2">
                                    <strong>{`user_${c.member_no}`}</strong> {c.content}
                                </Typography>
                            </Box>
                        ))}
                    </Box>

                    {/* 5.   */}
                    <Box sx={{ px: 2, py: 1, borderTop: "1px solid #eee", display: "flex", gap: 1 }}>
                        <TextField
                            variant="standard"
                            placeholder=" ..."
                            fullWidth
                            value={comment}
                            onChange={(e) => setComment(e.target.value)}
                            sx={{
                                "& .MuiInput-underline:after": {
                                    borderBottomColor: "#94B8C4",
                                },
                            }}
                        />
                        <Button
                            variant="text"
                            onClick={handleAddComment}
                            sx={{
                                color: "#707C5C",
                                fontWeight: "bold",
                                "&:hover": {
                                    color: "#5e6a4a",
                                },
                            }}
                        >
                            
                        </Button>
                    </Box>
                </Box>
            </Box>
        </Box>
    );
}

export default FeedDetail;
