import React from "react";
import { Box, Avatar, Typography, Button } from "@mui/material";

const ProfileHeader = () => {
  // TODO:    props recoil   
  const profile = {
    nickname: "Mubok_",
    intro: "   .        !",
    postCount: 3,
    followerCount: 100,
    followingCount: 100,
    profileImg: "", //   
  };

  return (
    <Box sx={{ display: "flex", alignItems: "center", mb: 4 }}>
      {/*   */}
      <Avatar
        sx={{ width: 120, height: 120, mr: 4 }}
        src={profile.profileImg || ""}
      />

      {/*  +  +  +  */}
      <Box>
        {/*  +   */}
        <Box sx={{ display: "flex", alignItems: "center", gap: 2, mb: 1 }}>
          <Typography variant="h5" fontWeight="bold">
            {profile.nickname}
          </Typography>
          <Button
            variant="outlined"
            size="small"
            sx={{
              borderColor: "#707C5C",
              color: "#707C5C",
              textTransform: "none",
              fontWeight: 500,
              px: 2,
              py: 0.5,
              fontSize: "0.8rem",
              borderRadius: "12px",
            }}
          >
             
          </Button>
        </Box>

        {/*   */}
        <Box sx={{ display: "flex", gap: 3, mb: 1 }}>
          <Typography variant="body2"> {profile.postCount}</Typography>
          <Typography variant="body2"> {profile.followingCount}</Typography>
          <Typography variant="body2"> {profile.followerCount}</Typography>
        </Box>

        {/*  */}
        <Typography variant="body2" color="text.secondary">
          {profile.intro}
        </Typography>
      </Box>
    </Box>
  );
};

export default ProfileHeader;
