import React from "react";
import { Box, Typography, List, ListItemButton, ListItemIcon, ListItemText, Badge } from "@mui/material";
import HomeRoundedIcon from "@mui/icons-material/HomeRounded";
import SendRoundedIcon from "@mui/icons-material/SendRounded";
import NotificationsNoneRoundedIcon from "@mui/icons-material/NotificationsNoneRounded";
import AddBoxRoundedIcon from "@mui/icons-material/AddBoxRounded";
import PersonOutlineRoundedIcon from "@mui/icons-material/PersonOutlineRounded";
import { useNavigate } from "react-router-dom";


function Sidebar() {
    const navigate = useNavigate(); //  useNavigate  

    return (
        <Box
            sx={{
                height: "100%",
                backgroundColor: "#FDFBF5",
                padding: "30px 20px",
                borderRight: "1px solid #ddd",
                position: "sticky",  //  
                top: "0",
            }}
        >
            {/*  +  */}
            <Box sx={{ textAlign: "center", marginBottom: "40px" }}>
                <img src="/img/mubok-logo1.png" alt=" " width="120" style={{ marginBottom: "10px" }} />
                <Typography variant="subtitle2" color="text.secondary" sx={{ fontSize: "12px" }}>
                      ,<br />   
                </Typography>
            </Box>

            {/*   */}
            <List>
                <ListItemButton onClick={() => navigate("/home")}>
                    <ListItemIcon>
                        <HomeRoundedIcon />
                    </ListItemIcon>
                    <ListItemText primary="" />
                </ListItemButton>

                <ListItemButton>
                    <ListItemIcon>
                        <SendRoundedIcon />
                    </ListItemIcon>
                    <ListItemText primary="" />
                </ListItemButton>

                <ListItemButton>
                    <ListItemIcon>
                        <Badge badgeContent={5} color="error">
                            <NotificationsNoneRoundedIcon />
                        </Badge>
                    </ListItemIcon>
                    <ListItemText primary="" />
                </ListItemButton>

                <ListItemButton onClick={() => navigate("/feedWrite")}>
                    <ListItemIcon>
                        <AddBoxRoundedIcon />
                    </ListItemIcon>
                    <ListItemText primary="" />
                </ListItemButton>

                <ListItemButton onClick={() => navigate("/profile")}>
                    <ListItemIcon>
                        <PersonOutlineRoundedIcon sx={{ color: "#aaa" }} />
                    </ListItemIcon>
                    <ListItemText primary="" />
                </ListItemButton>
            </List>
        </Box>
    );
}

export default Sidebar;
