import React from "react";
import { useNavigate, useLocation } from "react-router-dom";
import { useEffect, useState } from "react";
import { useRecoilValue } from "recoil";
import { userState } from "../recoil/atoms";

//mui
import Button from "@mui/material/Button";
import {
  Box,
  Card,
  CardMedia,
  CardContent,
  Typography,
  IconButton,
  Avatar,

} from "@mui/material";
import FavoriteBorderIcon from "@mui/icons-material/FavoriteBorder";
import ChatBubbleOutlineIcon from "@mui/icons-material/ChatBubbleOutline";
import SendIcon from "@mui/icons-material/Send";
import BookmarkBorderIcon from "@mui/icons-material/BookmarkBorder";


function FeedCard({ feed }) {
  const navigate = useNavigate();
  const location = useLocation();
  const user = useRecoilValue(userState);
  const currentUserId = user.member_no;
  const [isFollowing, setIsFollowing] = useState(false);

  useEffect(() => {
    if (currentUserId !== feed.member_no) {
      fetch(`http://13.125.231.25:3005/follow/check?from=${currentUserId}&to=${feed.member_no}`)
        .then(res => res.json())
        .then(data => setIsFollowing(data.result === 1)); // result: 1  
    }
  }, [currentUserId, feed.member_no]);

  const handleFollowToggle = (e) => {
    e.stopPropagation(); //    

    const method = isFollowing ? "DELETE" : "POST";
    fetch(`http://13.125.231.25:3005/follow`, {
      method: method,
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        follower_no: currentUserId,
        following_no: feed.member_no,
      }),
    })
      .then((res) => res.json())
      .then(() => setIsFollowing(!isFollowing));
  };



  return (
    <Card
      onClick={() => navigate(`/feed/${feed.feed_no}`, {
        state: { backgroundLocation: location },
      })}
      sx={{ cursor: "pointer", margin: "14px" }}
    >

      {/*    */}
      <CardContent sx={{ display: "flex", alignItems: "center", padding: "14px" }}>
        <Box sx={{ display: "flex", alignItems: "center", gap: 1 }}>
          <Avatar
            src={
              user.profile_img
                ? `http://13.125.231.25:3005/uploads/profile/${user.profile_img}`
                : "default.png"
            }
          />
          <Typography fontWeight="bold">{feed.nickname}</Typography>
          {/*     */}
          {currentUserId !== feed.member_no && (
            <Button
              size="small"
              onClick={handleFollowToggle}
              sx={{
                ml: 1,
                backgroundColor: isFollowing ? "#fff" : "#707C5C",
                color: isFollowing ? "#707C5C" : "#fff",
                border: "1px solid #707C5C",
                "&:hover": {
                  backgroundColor: isFollowing ? "#f2f2f2" : "#5a6b4f", //   
                },
                textTransform: "none", //  
                fontWeight: 500,
                fontSize: "0.75rem",
                padding: "4px 10px",
                borderRadius: "12px",
                minWidth: "auto",
              }}
            >
              {isFollowing ? "" : ""}
            </Button>
          )}
          <Typography variant="caption" color="text.secondary">
            {new Date(feed.cdatetime).toLocaleDateString()}
          </Typography>
        </Box>
      </CardContent>


      {/*  */}
      <Box
        sx={{
          width: "100%",
          aspectRatio: "1 / 1", //    (   fallback )
          backgroundColor: "#000",
          overflow: "hidden",
        }}
      >

        <img
          src={`http://13.125.231.25:3005/${feed.file_path}`}
          alt=" "
          style={{
            width: "100%",
            height: "100%",
            objectFit: "cover", //     
            display: "block",
          }}
        />
      </Box>



      {/*   */}
      <Box sx={{ display: "flex", justifyContent: "space-between", px: 2, pt: 1 }}>
        <Box>
          <IconButton><FavoriteBorderIcon /></IconButton>
          <IconButton><ChatBubbleOutlineIcon /></IconButton>
          <IconButton><SendIcon /></IconButton>
        </Box>
        <IconButton>
          <BookmarkBorderIcon />
        </IconButton>
      </Box>

      {/*  +  */}
      <CardContent sx={{ pt: 0 }}>
        {/*  */}
        <Typography variant="subtitle2" sx={{ fontWeight: 600 }}>
           50
        </Typography>

        {/*  ( ) */}
        <Typography variant="body2">
          <strong>{feed.nickname}</strong>{" "}
          {feed.content.slice(0, 30)}
          <span style={{ color: "#888", cursor: "pointer" }}>... </span>
        </Typography>

        {/*    */}
        <Typography variant="body2" sx={{ color: "#888", mt: 1 }}>
           4  
        </Typography>

        {/*   */}
        <Typography variant="body2" sx={{ color: "#888", mt: 0.5 }}>
           ...
        </Typography>
      </CardContent>
    </Card>
  );
}

export default FeedCard;
