import React, { useState } from "react";
import {
    Box,
    Button,
    TextField,
    Typography,
    Paper,
    Link,
} from "@mui/material";
import { useNavigate } from "react-router-dom";
import { useSetRecoilState } from "recoil";
import { userState } from "../recoil/atoms";
import { jwtDecode } from "jwt-decode";


function Login() {
    const navigate = useNavigate();
    const setUser = useSetRecoilState(userState);


    const [email, setEmail] = useState("");
    const [pwd, setPwd] = useState("");

    const handleLogin = () => {
        fetch("http://13.125.231.25:3005/user/login", {
            method: "POST",
            headers: {
                "Content-type": "application/json"
            },
            body: JSON.stringify({ email, pwd })
        })
            .then(res => res.json())
            .then(data => {
                if (data.success) {
                    localStorage.setItem("token", data.token);
                    const decoded = jwtDecode(data.token);

                    setUser({
                        isLogin: true,
                        member_no: decoded.member_no,
                        email: decoded.email,
                        nickname: decoded.nickname,
                        profile_img: decoded.profile_img,
                    });
                    navigate("/home");
                }
            })
    };

    return (
        <Box
            sx={{
                backgroundColor: "#fdfbf5",
                minHeight: "100vh",
                display: "flex",
                flexDirection: "column",
                alignItems: "center",
                justifyContent: "center",
            }}
        >
            <Paper
                elevation={3}
                sx={{
                    padding: 4,
                    width: 300,
                    textAlign: "center",
                    backgroundColor: "#fffef6",
                }}
            >
                <Typography
                    variant="subtitle1"
                    sx={{
                        mb: 2,
                        fontWeight: "bold",
                        color: "#768265",
                        borderRadius: "10px",
                        padding: "5px 10px",
                        fontSize: "0.9rem"
                    }}
                >
                      ,   
                </Typography>

                <img
                    src="/img/mubok-logo1.png"
                    alt=" "
                    style={{ width: 120, marginBottom: 16 }}
                />

                <TextField
                    fullWidth
                    label=""
                    variant="outlined"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    sx={{
                        mb: 2,
                        "& label.Mui-focused": {
                            color: "#768265", //    
                        },
                        "& .MuiOutlinedInput-root": {
                            "&.Mui-focused fieldset": {
                                borderColor: "#768265", //    
                            },
                        },
                    }}
                />
                <TextField
                    fullWidth
                    label=""
                    type="password"
                    variant="outlined"
                    value={pwd}
                    onChange={(e) => setPwd(e.target.value)}
                    onKeyUp={(e) => {
                        if (e.key === "Enter") {
                            handleLogin();
                        }
                    }}
                    sx={{
                        mb: 2,
                        "& label.Mui-focused": {
                            color: "#768265", //    
                        },
                        "& .MuiOutlinedInput-root": {
                            "&.Mui-focused fieldset": {
                                borderColor: "#768265", //    
                            },
                        },
                    }}
                />

                <Button
                    fullWidth
                    variant="contained"
                    onClick={handleLogin}
                    sx={{
                        backgroundColor: "#94b8c4", //  Sky Blue
                        color: "white",
                        fontWeight: "bold",
                        mb: 1,
                        "&:hover": {
                            backgroundColor: "#4f7f91", // hover  Bike Blue
                        },
                    }}
                >
                    
                </Button>

                <Box
                    sx={{
                        borderTop: "1px solid #ccc",
                        my: 2,
                        width: "100%",
                        marginTop: "46px",
                        marginBottom: "40px",
                    }}
                />


                <Typography variant="body2" sx={{ mb: 1 }}>
                     ?
                </Typography>

                <Typography variant="body2">
                     ?{" "}
                    <Link href="/register" underline="hover">
                        
                    </Link>
                </Typography>
            </Paper>
        </Box>
    );
}

export default Login;